@import '../../global.scss';

.newsContainer {
    position: relative;
    width: 100%;
    overflow-x: hidden;

    header {
        width: 100%;
        position: relative;
        overflow: hidden;

        img {
            width: 100%;
            object-fit: cover;
            filter: blur(3px) brightness(0.5);

            @media (max-width: 768px) {
                height: 250px;
            }

            @media (min-width: 769px) {
                height: 500px;
            }
        }
    }

    .news {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        padding: 80px $page-padding-x-dl;
        box-sizing: border-box;
        width: 100%;

        @media (max-width: 768px) {
            padding: 40px $page-padding-x-m;
        }

        @media (min-width: 769px) {
            padding: 80px $page-padding-x-dl;
        }

        header {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 4rem;
            width: 100%;
            max-width: 1200px;
            position: relative;

            @media (max-width: 768px) {
                flex-direction: column;
                gap: 1rem;
                margin-bottom: 2rem;
            }

            h3 {
                font-size: 1.75rem;
                font-weight: 500;
                margin: 0 2rem;
                cursor: pointer;
                text-align: center;
                color: #333;
                transition: color 0.3s ease;
                position: relative;
                padding: 0.5rem 0;

                @media (max-width: 768px) {
                    font-size: 1.5rem;
                    margin: 0;
                    width: 100%;
                }

                &:hover {
                    color: #000;
                }
            }

            .split {
                width: 1px;
                height: 32px;
                background: #ddd;

                @media (max-width: 768px) {
                    display: none;
                }
            }

            .underline {
                position: absolute;
                bottom: -2px;
                left: 0;
                width: 50%;
                height: 3px;
                background: $aerovect-blue;
                transition: transform 0.3s ease;
                border-radius: 2px;

                &.right {
                    transform: translateX(100%);
                }
            }
        }

        .coverageList {
            width: 100%;
            max-width: 1400px;
            margin: 0 auto;

            @media (min-width: 1400px) {
                max-width: 90vw;
            }

            .coverage {
                display: flex;
                align-items: center;
                padding: 3rem 3rem 3rem 0;
                border-bottom: 1px solid #eee;
                width: 100%;
                box-sizing: border-box;
                transition: background-color 0.3s ease;

                @media (max-width: 768px) {
                    flex-direction: column;
                    padding: 1.5rem;
                    align-items: flex-start;
                    gap: 1rem;
                }

                &:hover {
                    background-color: #f8f9fa;
                }

                img {
                    width: 160px;
                    height: 160px;
                    object-fit: contain;
                    margin-right: 3rem;
                    border-radius: 8px;
                    background-color: white;
                    padding: 1.5rem;

                    @media (max-width: 768px) {
                        width: 80px;
                        height: 80px;
                        margin-right: 0;
                        margin-bottom: 0.5rem;
                    }
                }

                .article {
                    flex: 1;

                    @media (max-width: 768px) {
                        width: 100%;
                    }

                    h4 {
                        font-size: 1.4rem;
                        font-weight: 500;
                        margin-bottom: 0.75rem;
                        color: #1a1a1a;
                        line-height: 1.4;

                        @media (max-width: 768px) {
                            font-size: 1.2rem;
                            margin-bottom: 0.5rem;
                        }
                    }

                    p {
                        color: #666;
                        font-size: 1rem;
                        line-height: 1.5;

                        @media (max-width: 768px) {
                            font-size: 0.95rem;
                        }
                    }
                }

                a {
                    display: inline-flex;
                    align-items: center;
                    text-decoration: none;
                    color: $aerovect-blue;
                    font-weight: 500;
                    font-size: 0.95rem;
                    padding: 0.6rem 1.2rem;
                    border-radius: 6px;
                    transition: all 0.2s ease;
                    margin-left: 2rem;
                    background-color: rgba($aerovect-blue, 0.08);
                    border: 1px solid rgba($aerovect-blue, 0.2);

                    @media (max-width: 768px) {
                        margin-left: 0;
                        width: 100%;
                        justify-content: center;
                        padding: 0.8rem 1.2rem;
                    }

                    &:hover {
                        background-color: rgba($aerovect-blue, 0.12);
                        border-color: rgba($aerovect-blue, 0.3);
                        transform: translateY(-1px);
                    }

                    p {
                        margin: 0;
                        margin-right: 0.5rem;
                        font-size: 0.95rem;
                        color: $aerovect-blue;
                    }

                    img {
                        width: 16px;
                        height: 16px;
                        margin: 0;
                        padding: 0;
                        box-shadow: none;
                        background: none;
                    }
                }
            }
        }

        .mediaKit {
            width: 100%;
            max-width: 1200px;
            display: flex;
            flex-direction: column;
            gap: 40px;

            @media (max-width: 768px) {
                gap: 24px;
            }

            .mediaRow {
                display: flex;
                justify-content: space-between;
                gap: 30px;
                width: 100%;

                @media (max-width: 768px) {
                    flex-direction: column;
                    gap: 24px;
                }

                .media {
                    flex: 1;
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    border-radius: 16px;
                    overflow: hidden;
                    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
                    transition: transform 0.3s ease;

                    @media (max-width: 768px) {
                        width: 100%;
                        margin: 0 auto;
                        max-width: 400px;
                    }

                    &:hover {
                        transform: translateY(-4px);
                    }

                    .mediaKitImg {
                        width: 100%;
                        max-width: 350px;
                        height: 246px;
                        object-fit: cover;
                        border-radius: 16px;

                        @media (max-width: 768px) {
                            height: 200px;
                            max-width: 100%;
                        }
                    }

                    .hoveredImg {
                        position: absolute;
                        top: 0;
                        right: 0;
                        height: 100%;
                        width: 50%;
                        background: linear-gradient(270deg, rgba($aerovect-blue, 0.9) 0%, rgba($aerovect-blue, 0) 100%);
                        opacity: 0;
                        transition: all 0.3s ease;
                        pointer-events: none;
                        border-top-right-radius: 16px;
                        border-bottom-right-radius: 16px;
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;
                        padding-right: 2rem;

                        @media (max-width: 768px) {
                            width: 100%;
                            background: linear-gradient(180deg, rgba($aerovect-blue, 0.9) 0%, rgba($aerovect-blue, 0) 100%);
                            border-radius: 0;
                            padding-right: 0;
                            padding-bottom: 2rem;
                            justify-content: center;
                        }

                        img {
                            width: 32px;
                            height: 32px;
                            filter: brightness(0) invert(1);
                        }
                    }
                }

                .media:hover .hoveredImg {
                    opacity: 1;
                    pointer-events: initial;
                }
            }
        }
    }
}